@import "./../../AppVariables";

.dialogs {
  display: grid;
  grid-template-columns: $minmaxGridColumns minmax(300px, 10fr);
  grid-gap: $padding; }

.dialogsItems {
  background-color: $beige;
  padding: $padding; }

.messages {
  padding: $padding; }


.sendBar {
  padding: 0 0 $padding 0; }

.sendBar textarea {
  width: calc(100% - 20px); }
